*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
p{
    font-size: 2rem;
}
h1{
    font-size: 5rem;
}
.App {
    width: 100%;
    height: 100vh;
    position: relative;
    background-color: rgba(0, 0, 0, 0.4);
    color: #fff;
}

.App:before {
    content: '';
    background: url('/src/images/cloudy.jpg') no-repeat center center/cover;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}

.App .search {
    text-align: center;
    padding: 1rem;
}

.App input {
    padding: .7rem 1.5rem;
    font-size: 1.2rem;
    border-radius: 25px;
    border: 1px solid rgba( 255, 255, 255, 0.8);
    background: rgba( 255, 255, 255, 0.1);
    color: #fff;
}

.container {
    max-width: 700px;
    height: 700px;
    margin: auto;
    padding: 0 1rem;
    position: relative;
    top: 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.App .top {
    width: 100%;
    margin: 1rem auto;
}

.App .description {
    position: relative;
    right: -90%;
    transform-origin: 0 0;
    transform: rotate(269deg);
}
.App .bottom {
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 12px;
    background-color: rgba( 255, 255, 255, 0.2);
}

.bold {
    font-weight: 700;
}